/* eslint-disable */

import React, { useState, useContext, lazy, Suspense } from 'react'
import { Navbar, Container, Nav, NavDropdown, Button} from 'react-bootstrap';
import './App.css';
import Data from './data.js'
//import DetailPage from './Detail.js'
import axios from 'axios'
import { Link, Route, Switch} from 'react-router-dom'

// import Cart from './Cart.js'
import { useHistory } from 'react-router-dom';


let DetailPage = lazy(() => import('./Detail.js'))
let Cart = lazy(() => import('./Cart.js'))


//  
export let ItemContext = React.createContext()

function App() {

  let [product, product] = useState(Data)
  let [loading, setLoading] = useState(false)
  let [item, setItem] = useState([10,11,12,13,14,15])
  let [imgIndex, setImgIndex] = useState(0)
  let [moreBtn, setMoreBtn] = useState(0)


  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand><Link to="/" className="title">DM_PortFolio</Link></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">Home</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    <Switch>
      
      <Route exact path="/">
        <JumboTron></JumboTron>
        <div className="container">

        {/* props  Context API */}
        
        <ItemContext.Provider value={item}>
          <div className="row">
            {
              product.map((item, index) => {
                return (
                  <ProductDetail product={product[index]} i={index} key={index}></ProductDetail>
                )
              })
            }
          </div>
          </ItemContext.Provider>


          {
            loading
            ? <Loading></Loading>
            : null
            
          }

         
          
          
        </div>
      </Route>

      <Route path="/detail/:id">
        <ItemContext.Provider value={item}>
          <Suspense fallback={<div></div>}>
            <DetailPage product={product} item={item} setItem={setItem} i={imgIndex}></DetailPage>
          </Suspense>  
        </ItemContext.Provider>
      </Route>

      <Route path="/cart">
        <Suspense fallback={<div></div>}>
          <Cart></Cart>
        </Suspense>
      </Route>

    </Switch>

    </div>    
  )
}

function Loading() {
  return (
    <div> ...</div>
  )
}

function ProductDetail(props) {

  let Item = useContext(ItemContext)
  let history = useHistory()

  return (
    <div className="col-md-4 product" >
      <div className="product_wrap">
        <div>
          <img src={'https://dongmyounglee.github.io/port/project' + (props.i+1) + '.png'} width="80%" />
        </div>
        
        <h4 className="title">{props.product.title}</h4>
        <a href={props.product.url} target="_blank" className="link1">
          <p>{props.product.link}</p>
        </a>
        <p onClick={() => {history.push('/detail/' + props.product.id)}} className="link2">{props.product.link2}</p>
      </div>      
    </div>
  )
}

function JumboTron() {
  return (
    <div className="jumbotron">
      <div className='jumbo_wrap'>
        <div>
          <img src="https://dongmyounglee.github.io/img/test.jpg" alt="DM_Photo" />
        </div>
        <div className='secondDiv'>
          <p></p>
          <p>FE   .</p>
          <a href="https://github.com/dongmyounglee" target="_blank">
            <img src="https://dongmyounglee.github.io/img/git.png" alt="github" className='imgTarget' />
          </a>
          <p className='tel'>tel : 010-5540-1958</p>
        </div>
      </div>
    </div>
  )
}



export default App;
